.card {
    width: 350px;
    height: 264px;
    background-color: #030B0f;
    padding: .3em;
    margin: 30px;
    border-radius: 6px;
  }
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px;
  }
  .card-image {
    background-image: url('one.jpg');
    width: 100%;
    height: 190px;
    border-radius: 6px 6px ;
  }
  .card-image1 img {
    background-image: url('one.jpg');
    width: 100%;
    height: 190px;
    border-radius: 6px 6px ;
  }
  .card-image:hover {
    transform: scale(0.98);
  }
  

  .heading {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    color: rgb(241, 241, 241);
    margin: 2px;
    padding: 3px;
  }
  
  .heading-text {
    font-weight: 600;
    font-size: 30px;
    color: rgb(241, 241, 241);
  }

  .heading:hover {
    cursor: pointer;
  }
  

 
  .signup-btn {
    background-color: rgb(28, 206, 28);
    color: rgb(0, 0, 0);
    border: none;
    border-radius: 30px;  
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border-radius: 20px;
    color: white;
    padding: 10px 20px;
    margin-left: 30px;
    text-align: center;
    transition: all 0.2s ease-in-out;
  }

 
  
  .signup-btn:hover {
    background-color: green;
    cursor: pointer;
  }
  
  .signup-btn:active {
    transform: scale(0.95);
  }