
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    /* background-color: #1b1f22; */
    background-color: #030B0f;
    /* background-color: #34495e; */
    color: #ffffff;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .nav-links {
    display: flex;
    justify-content: flex-start ;
    align-items: center;
  }
  
  .nav-links a {
    margin-right: 10px;
    margin-left: 20px;
    color: #ffffff;
    text-decoration: none;
  }
 
  
  .nav-links a:hover {
    color: hsl(153, 100%, 50%);   
    cursor: pointer;
  }
  
   
  

  .search-container {
    background-color: #505050;
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
  }
  
  .search-container input {
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 16px;
    color: hsl(153, 100%, 50%);
    flex: 1;
    padding-left: 10px;
  }
  
  .search-container i {
    color: #fff;
    font-size: 20px;
    padding-right: 10px;
  }
  

