  .signup-btn {
    background-color: rgb(28, 206, 28);
    color: rgb(0, 0, 0);
    border: none;
    border-radius: 30px;  
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border-radius: 20px;
    color: white;
    padding: 5px 20px;
    text-align: center;
    transition: all 0.2s ease-in-out;
  }

 
  
  .signup-btn:hover {
    background-color: green;
    cursor: pointer;
  }
  
  .signup-btn:active {
    transform: scale(0.95);
  }
  
  .center-container {
    display:flex;
    justify-content: flex-end;
    align-items: center;
  }